import ChuanDauRaList from "components/common/ChuanDauRaList/ChuanDauRaList";
import i18n from "i18n/i18n";
import React, { useEffect, useState } from "react";
import { toast } from "react-toastify";

const ChuanDauRa = (props) => {
    const [data, setData] = useState(
        props.getState("noiDungChuongTrinh") || props.values
    );

    const handleSubmit = async () => {
        try {
            props.saveValue(props.saveKey, [...data]);
            props.setState("chuanDauRaNganhDaoTao", data);
            props.next();
        } catch (error) {
            toast.error(i18n.t('RESPONSE_MESSAGE500'));
            return;
        }
    };

    const handleSave = () => {
        props.saveValue(props.saveKey, [...data]);
        toast.success(i18n.t("admin.UPDATE_SUCCESS"));
    };

    const handleSetData = (data) => {
        setData(data);
    };

    return (
        <>
            <ChuanDauRaList
                values={data}
                handleSave={handleSave}
                handleSubmit={handleSubmit}
                handleSetData={handleSetData}
            />
        </>
    );
};

export default ChuanDauRa;
