import axiosClient from "api/common/axiosClient";
import CONFIG from "config/config";
import i18n from "i18n/i18n";
import queryString from "query-string";
import { toast } from "react-toastify";

export default {
    getAll: async function (filter) {
        const query = queryString.stringify({ ...filter });
        try {
            const url = `${CONFIG.API.EXPORT_PDF}/data?${query}`;
            return await axiosClient.get(url);
        } catch (error) {
            toast.error(i18n.t("ERROR"));
        }
    },
    createPdf: async (data) => {
        try {
            const url = `${CONFIG.API.EXPORT_PDF}/data`;
            return await axiosClient.post(url, { data });
        } catch (error) {
            toast.error(i18n.t("ERROR"));
        }
    },
    getJSON: async function (filter) {
        const query = queryString.stringify({ ...filter });
        try {
            const url = `${CONFIG.API.EXPORT_PDF}/json?${query}`;
            return await axiosClient.get(url);
        } catch (error) {
            toast.error(i18n.t("ERROR"));
        }
    },
    exportPdfFromJSON: async (data, opts = {}) => {
        try {
            const url = `${CONFIG.API.EXPORT_PDF}/json/pdf`;
            return await axiosClient.post(url, data, { ...opts });
        } catch (error) {
            toast.error(i18n.t("ERROR"));
        }
    },
};
