import React, { useEffect, useState } from "react";
import { Button } from "reactstrap";
import Styles from "../../FlowCreateCTDT/component/styled";
import TablesSelection from "../../FlowCreateCTDT/component/TablesSelection";

const NoiDungChuongTrinh = ({ columns, subjects, modifyData }) => {
    const [data, setData] = useState(subjects);
    const [selectedRowIndex, setSelectedRowIndex] = useState([]);

    useEffect(() => {
        setData(subjects);
    }, [subjects]);

    const updateMyData = async (rowIndex, columnId, value) => {
        const newListSubject = data.map((row, index) => {
            if (index === rowIndex) {
                if (columnId.includes(".")) {
                    const [, property] = columnId.split(".");
                    return {
                        ...data[rowIndex],
                        idMH: {
                            ...data[rowIndex].idMH,
                            [property]: value,
                        },
                    };
                }
                return {
                    ...data[rowIndex],
                    [columnId]: value,
                };
            }
            return row;
        });
        modifyData(newListSubject);
    };

    const handleSelectedRow = (listSelectedRowId) => {
        const converListSelectedRowId = listSelectedRowId.map((id) =>
            Number(id)
        );
        setSelectedRowIndex(converListSelectedRowId);
    };

    const deleteRows = () => {
        const newListSubject = data.filter((row, index) => {
            if (selectedRowIndex.includes(index)) {
                return false;
            }
            return true;
        });
        modifyData(newListSubject);
    };

    return (
        <div>
            <Styles>
                <div
                    style={{
                        display: "flex",
                        padding: "0 0 10px 0",
                    }}
                >
                    <Button
                        onClick={deleteRows}
                        color="danger"
                        style={{
                            marginLeft: "10px",
                        }}
                    >
                        Delete rows
                    </Button>
                </div>
                <TablesSelection
                    columns={columns}
                    data={data}
                    updateMyData={updateMyData}
                    skipPageReset={true}
                    isPagination={false}
                    handleSelectedRow={handleSelectedRow}
                />
            </Styles>
        </div>
    );
};

export default NoiDungChuongTrinh;
