import React, { useEffect, useState } from "react";
import i18n from "i18n/i18n";
import { Button } from "reactstrap";
import Table from "../../FlowCreateCTDT/component/Tables";
import Styles from "../../FlowCreateCTDT/component/styled";
import { toast } from "react-toastify";

const CauTrucChuongTrinh = (props) => {
    const columns = React.useMemo(
        () => [
            {
                Header: "Khối Kiến Thức",
                accessor: "ten",
                width: 200,
            },
            {
                Header: "Số Tín Chỉ",
                columns: [
                    {
                        Header: "Bắt Buộc",
                        accessor: "tinChiBatBuoc",
                        width: 100,
                        isValidate: true,
                    },
                    {
                        Header: "Tự Chọn",
                        accessor: "tinChiTuChon",
                        width: 100,
                        isValidate: true,
                    },
                    {
                        Header: "Tự Chọn Tự Do",
                        accessor: "tinChiTuChonTuDo",
                        width: 150,
                        isValidate: true,
                    },
                    {
                        Header: "Tổng Cộng",
                        accessor: "tongTinChi",
                        disabled: true,
                        width: 100,
                        isValidate: true,
                    },
                ],
            },
            {
                Header: "Ghi Chú",
                accessor: "ghiChu",
                width: 350,
            },
        ],
        []
    );

    const [data, setData] = useState(
        props?.getState("cauTrucChuongTrinh") || props.values
    );

    const updateMyData = (rowIndex, columnId, value) => {
        const newData = data.map((row, index) => {
            if (index === rowIndex) {
                let convertValue = value;
                if (columnId !== "ghiChu" && columnId !== "ten") {
                    data[rowIndex].tongTinChi =
                        Number(data[rowIndex].tinChiBatBuoc) +
                        Number(data[rowIndex].tinChiTuChon) +
                        Number(data[rowIndex].tinChiTuChonTuDo) +
                        Number(value) -
                        Number(data[rowIndex][columnId]);
                    convertValue = Number(convertValue);
                }
                return {
                    ...data[rowIndex],
                    [columnId]: convertValue,
                };
            }
            return row;
        });

        setData(newData);
        // const totalTC = sumTc(newData);
        // setTotal(totalTC);
    };

    const handleSubmit = () => {
        props.saveValue("khoiKienThuc", [...data]);
        props.setState(props.saveKey, [...data]);
        props.next();
    };

    const handleSave = () => {
        props.saveValue("khoiKienThuc", [...data]);
        toast.success(i18n.t("admin.UPDATE_SUCCESS"));
    };

    return (
        <div>
            <div className="cautruc_btn_header">
                <h2>{i18n.t("STRUCTURE_BASIC")}</h2>
            </div>

            <Styles>
                <div className="styles_btn_top">
                    <Button
                        className="styles_btn_ submit_btn"
                        onClick={handleSave}
                        color="warning"
                    >
                        Save
                    </Button>
                    <Button
                        className="styles_btn_ submit_btn"
                        onClick={handleSubmit}
                        color="success"
                        style={{ marginLeft: "10px" }}
                    >
                        Submit
                    </Button>
                </div>

                <Table
                    columns={columns}
                    data={data}
                    updateMyData={updateMyData}
                    isPagination={false}
                />
            </Styles>
        </div>
    );
};

export default CauTrucChuongTrinh;
