import React, { useEffect, useState } from "react";
import { Formik, Form, Field } from "formik";
import { FormGroup, Label, Button } from "reactstrap";
import * as Yup from "yup";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";
import "../../CreateCTDT/CreateCTDT.css";

const formField = [
    {
        label: i18n.t("PROGRAM_CODE"),
        name: "maNganhDaoTao",
        id: "maNganhDaoTao",
        placeholder: "",
        field: "maNganhDaoTao",
    },
    {
        label: i18n.t("TYPE_EDUCATION"),
        name: "loaiHinh",
        id: "loaiHinh",
        placeholder: "",
        field: "loaiHinh",
    },
    {
        label: i18n.t("PROGRAM_NAME"),
        name: "tenNganhDaoTao",
        id: "tenNganhDaoTao",
        placeholder: "",
        field: "tenNganhDaoTao",
    },
    {
        label: i18n.t("UNIVERSITY_DEGREE"),
        name: "trinhDo",
        id: "trinhDo",
        placeholder: "",
        field: "trinhDo",
    },
    {
        label: i18n.t("TOTAL_CREDIT"),
        name: "tongTinChi",
        id: "tongTinChi",
        placeholder: "",
        field: "tongTinChi",
    },
    {
        label: i18n.t("AUDIENCE"),
        name: "doiTuong",
        id: "doiTuong",
        placeholder: "",
        field: "doiTuong",
    },
    {
        label: i18n.t("TRAINING_PROCESS"),
        name: "quiTrinhDaoTao",
        id: "quiTrinhDaoTao",
        placeholder: "",
        field: "quiTrinhDaoTao",
        as: "textarea",
    },
    {
        label: i18n.t("GRADUATION_CONDITIONS"),
        name: "dieuKienTotNghiep",
        id: "dieuKienTotNghiep",
        placeholder: "",
        field: "dieuKienTotNghiep",
        as: "textarea",
    },
];

const formSchema = Yup.object().shape({
    maNganhDaoTao: Yup.string().required(i18n.t("REQUIRED")),
    tenNganhDaoTao: Yup.string().required(i18n.t("REQUIRED")),
    loaiHinh: Yup.string().required(i18n.t("REQUIRED")),
    trinhDo: Yup.string().required(i18n.t("REQUIRED")),
    tongTinChi: Yup.number()
        .typeError(i18n.t("MUST_BE_NUMBER"))
        .required(i18n.t("REQUIRED")),
    doiTuong: Yup.string().required(i18n.t("REQUIRED")),
    quiTrinhDaoTao: Yup.string().required(i18n.t("REQUIRED")),
    dieuKienTotNghiep: Yup.string().required(i18n.t("REQUIRED")),
});

const Step1_Wrappers = (props) => {
    const [data, setdata] = useState(props.values);

    const handleSubmit = async (formData, actions) => {
        try {
            let { tongTinChi } = formData;
            if (typeof tongTinChi !== "number") {
                tongTinChi = Number(tongTinChi);
            }
            const newData = { ...formData, tongTinChi };
            setdata(newData);
            props.saveValue("", newData);
            toast.success(i18n.t("SUCCESS"));
            props.next();
        } catch (error) {
            toast.error(i18n.t("FAILED"));
        }
    };

    return (
        <div className="basic_info_container">
            <Formik
                initialValues={data}
                enableReinitialize={true}
                validationSchema={formSchema}
                onSubmit={handleSubmit}
            >
                {({ values, errors, handleChange, touched }) => (
                    <Form>
                        {errors?.exists ? (
                            <div className="error_message">
                                {errors?.exists}
                            </div>
                        ) : null}
                        {formField.map((item, index) => (
                            <FormGroup
                                className="basicinfo_row"
                                key={`basicinfo_row-${index}`}
                            >
                                <div className="basicinfo_content">
                                    <Label
                                        for={item.id}
                                        className="basicinfo_label"
                                    >
                                        {item.label}
                                    </Label>
                                    <Field
                                        as={item.as || "input"}
                                        name={item.name}
                                        id={item.id}
                                        placeholder={item.placeholder}
                                        value={values?.[item.field] || ""}
                                        onChange={handleChange}
                                        className="basicinfo_input"
                                    />
                                </div>
                                {errors[item.field] && touched[item.field] ? (
                                    <div
                                        className="error_message"
                                        style={{
                                            color: "#D8000C",
                                        }}
                                    >
                                        {errors[item.field]}
                                    </div>
                                ) : null}
                            </FormGroup>
                        ))}
                        <div className="basicinfo_row">
                            <Button type="submit">{i18n.t("SUBMIT")}</Button>
                        </div>
                    </Form>
                )}
            </Formik>
        </div>
    );
};

export default Step1_Wrappers;
