import React, { useEffect, useState } from "react";
import KeHoachGiangDay from "./KeHoachGiangDay";
import { Button } from "reactstrap";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";

const emptySubject = {
    maHP: "",
    tenHP: "",
    soTC: 0,
    tcLT: 0,
    tcTH: 0,
    tcBT: 0,
    loaiHP: "",
    ghiChu: "",
};

const KeHoachGiangDayWrapper = (props) => {
    const [data, setData] = useState(
        props.getState("keHoachGiangDay") || props.values
    );

    const modifyData = (hkIndex, newListSubject) => {
        const cloneData = [...data];
        cloneData[hkIndex].chiTietKHGD = [...newListSubject];
        setData(cloneData);
    };

    const handleSubmit = () => {
        props.setState("keHoachGiangDay", data);
        props.next();
    };

    const handleSave = () => {
        props.saveValue(props.saveKey, [...data]);
        toast.success(i18n.t("admin.UPDATE_SUCCESS"));
    };

    const renderContent = () => {
        return (
            <>
                <ol>
                    {data.map((hocKy, hkIndex) => (
                        <li key={`hocKy-${hkIndex}`}>
                            <p>Học kỳ: {hocKy.tenHocKy}</p>
                            <KeHoachGiangDay
                                columns={[
                                    {
                                        Header: "Ma hoc phan",
                                        accessor: "idCTGN.idMH.ma",
                                        width: 100,
                                    },
                                    {
                                        Header: "Ten hoc phan",
                                        accessor: "idCTGN.idMH.tenTiengViet",
                                        width: 450,
                                    },
                                    {
                                        Header: "So tin chi",
                                        accessor: "idCTGN.idMH.soTinChi",
                                        width: 80,
                                        isValidate: true,
                                    },
                                    {
                                        Header: "So tiet",
                                        columns: [
                                            {
                                                Header: "Ly thuyet",
                                                accessor:
                                                    "idCTGN.idMH.soTietLyThuyet",
                                                width: 80,
                                                isValidate: true,
                                            },
                                            {
                                                Header: "Thuc hanh",
                                                accessor:
                                                    "idCTGN.idMH.soTietThucHanh",
                                                width: 80,
                                                isValidate: true,
                                            },
                                            {
                                                Header: "Bai tap",
                                                accessor:
                                                    "idCTGN.idMH.soTietTuHoc",
                                                width: 80,
                                                isValidate: true,
                                            },
                                        ],
                                    },
                                    {
                                        Header: "Loai hoc phan",
                                        accessor: "idCTGN.idGN.loaiNhom",
                                        width: 80,
                                        // isSelect: true,
                                        // options: LOAI_HP,
                                    },
                                    {
                                        Header: "Ghi chu",
                                        accessor: "idCTGN.idGN.ghiChu",
                                        width: 200,
                                    },
                                ]}
                                subjects={hocKy.chiTietKHGD}
                                modifyData={(listSubject) =>
                                    modifyData(hkIndex, listSubject)
                                }
                            />
                        </li>
                    ))}
                </ol>
            </>
        );
    };

    return (
        <div>
            <div>
                <div className="kehoachgiangday_submit_btn">
                    <Button onClick={handleSave} color="warning">
                        Save
                    </Button>
                    <Button
                        onClick={handleSubmit}
                        color="success"
                        style={{ marginLeft: "10px" }}
                    >
                        Submit
                    </Button>
                </div>
            </div>
            {renderContent()}
        </div>
    );
};

export default KeHoachGiangDayWrapper;
