import React, { useEffect, useState } from "react";
import NoiDungChuongTrinh from "./NoiDungChuongTrinh";
import { Button } from "reactstrap";
import Loading from "components/common/loading/Loading";
import { toast } from "react-toastify";
import i18n from "i18n/i18n";

// import "./css/NoiDungChuongTrinhWrapper.css";

const emptyChiTietGomNhom = {
    idMH: null,
    ghiChu: "",
    monHoc: {
        ma: "",
        tenTiengViet: "",
        tenTiengAnh: "",
        soTinChi: 0,
        soTietLyThuyet: 0,
        soTietThucHanh: 0,
        soTietTuHoc: 0,
        id: null,
        isDeleted: false,
    },
};

const NoiDungChuongTrinhWrapper = (props) => {
    const [data, setData] = useState(
        props.getState("noiDungChuongTrinh") || props.values
    );
    const [isLoading, setIsLoading] = useState(false);

    const modifyData = async (
        kktIndex,
        lkktIndex,
        gomNhomIndex,
        newListSubject
    ) => {
        const cloneData = [...data];
        cloneData[kktIndex].loaiKhoiKienThuc[lkktIndex].gomNhom[
            gomNhomIndex
        ].chiTietGomNhom = [...newListSubject];
        setData(cloneData);
    };

    const handleSubmit = async () => {
        try {
            props.setState("noiDungChuongTrinh", data);
            // toast.success("Successfully");
            props.next();
        } catch (error) {
            toast.error(i18n.t('RESPONSE_MESSAGE500'));
            return;
        }
    };

    const handleSave = () => {
        props.saveValue(props.saveKey, [...data]);
        toast.success("SUCCESS");
    };

    // useEffect(() => {
    //     console.log(`props_step`, props);
    // }, []);

    const renderContent = () => {
        return (
            <>
                <ol>
                    {data.map((khoiKienThuc, kktIndex) => {
                        return (
                            <li key={`khoiKienThuc-${kktIndex}`}>
                                <p>{khoiKienThuc.ten}</p>
                                <ol>
                                    {khoiKienThuc.loaiKhoiKienThuc.map(
                                        (loaiKKT, lkktIndex) => (
                                            <li
                                                key={`khoiKienThuc-${kktIndex}loaiKKT-${kktIndex}-${loaiKKT.ten}`}
                                            >
                                                <p>{loaiKKT.ten}</p>
                                                <ol>
                                                    {loaiKKT.gomNhom.map(
                                                        (
                                                            gomNhomItem,
                                                            gomNhomIndex
                                                        ) => (
                                                            <li>
                                                                <p>
                                                                    Loại Nhóm:{" "}
                                                                    {
                                                                        gomNhomItem.loaiNhom
                                                                    }
                                                                </p>
                                                                <p>
                                                                    Tiêu đề:{" "}
                                                                    {gomNhomItem.tieuDe ||
                                                                        ""}
                                                                </p>
                                                                <NoiDungChuongTrinh
                                                                    columns={[
                                                                        {
                                                                            Header:
                                                                                "Ma hoc phan",
                                                                            accessor:
                                                                                "idMH.ma",
                                                                            width: 100,
                                                                        },
                                                                        {
                                                                            Header:
                                                                                "Ten hoc phan",
                                                                            accessor:
                                                                                "idMH.tenTiengViet",
                                                                            width: 450,
                                                                        },
                                                                        {
                                                                            Header:
                                                                                "So tin chi",
                                                                            accessor:
                                                                                "idMH.soTinChi",
                                                                            width: 80,
                                                                            isValidate: true,
                                                                        },
                                                                        {
                                                                            Header:
                                                                                "So tiet",
                                                                            columns: [
                                                                                {
                                                                                    Header:
                                                                                        "Ly thuyet",
                                                                                    accessor:
                                                                                        "idMH.soTietLyThuyet",
                                                                                    width: 80,
                                                                                    isValidate: true,
                                                                                },
                                                                                {
                                                                                    Header:
                                                                                        "Thuc hanh",
                                                                                    accessor:
                                                                                        "idMH.soTietThucHanh",
                                                                                    width: 80,
                                                                                    isValidate: true,
                                                                                },
                                                                                {
                                                                                    Header:
                                                                                        "Bai tap",
                                                                                    accessor:
                                                                                        "idMH.soTietTuHoc",
                                                                                    width: 80,
                                                                                    isValidate: true,
                                                                                },
                                                                            ],
                                                                        },
                                                                        // {
                                                                        //     Header:
                                                                        //         "Loai hoc phan",
                                                                        //     accessor:
                                                                        //         "loaiNhom",
                                                                        //     width: 80,
                                                                        //     isSelect: true,
                                                                        //     options: LOAI_HP,
                                                                        // },
                                                                        {
                                                                            Header:
                                                                                "Ghi chu",
                                                                            accessor:
                                                                                "ghiChu",
                                                                            width: 100,
                                                                        },
                                                                    ]}
                                                                    subjects={
                                                                        gomNhomItem.chiTietGomNhom
                                                                    }
                                                                    // addNewRow={() =>
                                                                    //     addNewRow(
                                                                    //         kktIndex,
                                                                    //         lkktIndex,
                                                                    //         gomNhomIndex
                                                                    //     )
                                                                    // }
                                                                    modifyData={(
                                                                        listSubject
                                                                    ) =>
                                                                        modifyData(
                                                                            kktIndex,
                                                                            lkktIndex,
                                                                            gomNhomIndex,
                                                                            listSubject
                                                                        )
                                                                    }
                                                                />
                                                            </li>
                                                        )
                                                    )}
                                                </ol>
                                            </li>
                                        )
                                    )}
                                </ol>
                            </li>
                        );
                    })}
                </ol>
            </>
        );
    };

    return (
        <>
            {isLoading ? (
                <Loading />
            ) : (
                <div>
                    <div>
                        <div className="cautrucchuongtrinh_btn">
                            <Button onClick={handleSave} color="warning">
                                Save
                            </Button>
                            <Button
                                onClick={handleSubmit}
                                color="success"
                                style={{ marginLeft: "10px" }}
                            >
                                Submit
                            </Button>
                            {/* <Button
                                onClick={() => setIsOpenCreateKKT(true)}
                                color="warning"
                            >
                                Add KKT
                            </Button> */}
                        </div>
                    </div>
                    {renderContent()}
                </div>
            )}
        </>
    );
};

export default NoiDungChuongTrinhWrapper;
