import React, { useEffect, useState } from "react";
import { Steps, Step } from "react-step-builder";
import Navigation from "./Navigation";
import Loading from "components/common/loading/Loading";
import Step1_Wrapper from "./Step1/Step1_Wrapper";
import CauTrucChuongTrinh from "./CauTrucChuongTrinh/CauTrucChuongTrinh";
import NoiDungChuongTrinhWrapper from "./NoiDungChuongTrinh/NoiDungChuongTrinhWrapper";
import KeHoachGiangDayWrapper from "./KeHoachGiangDay/KeHoachGiangDayWrapper";
import useSidebar from "stores/common/SidebarStore/SidebarStore";
import { useParams } from "react-router";
import { toast } from "react-toastify";
import exportAPI from "api/common/export.api";
import axios from "axios";
import CONFIG from "config/config";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PreviewPdf from "./PreviewPdf/PreviewPdf";
import ChuanDauRa from "./ChuanDauRa/ChuanDauRa";
import "./EditorWrapper.css";
import i18n from "i18n/i18n";

const EditorCTDTWrapper = (props) => {
    const params = useParams();
    const [stateSideBar, actionSideBar] = useSidebar();
    const [isLoading, setIsLoading] = useState(false);
    const [isProcessing, setIsProcessing] = useState(false);
    const [data, setData] = useState(null);
    const [fileName, setFileName] = useState("");
    const [urlPreviewPdf, setUrlPreviewPdf] = useState("");

    const config = {
        navigation: {
            component: Navigation,
            location: "before",
        },
    };

    useEffect(() => {
        (async () => {
            setIsLoading(true);
            if (params.id && params.khoa) {
                const { id: nganhDaoTao, khoa } = params;
                const result = await exportAPI.getJSON({ nganhDaoTao, khoa });
                setData(result?.data || "");
                setFileName(result?.fileName);
            } else {
                toast.error(i18n.t('RESPONSE_MESSAGE404'));
            }
            if (stateSideBar?.isOpenSideBar) {
                actionSideBar.setIsOpenSideBar({ isOpenSideBar: false });
            }
            setIsLoading(false);
        })();
    }, [params.id, params.khoa]);

    const saveValue = (key, value) => {
        if (key) {
            setData((oldValue) => {
                return { ...oldValue, [key]: value };
            });
        } else {
            setData(value);
        }
    };

    const exportPdf = async () => {
        setIsProcessing(true);
        const { id: nganhDaoTao, khoa } = params;
        const url = `${CONFIG.API.EXPORT_PDF}/json/pdf`;
        setUrlPreviewPdf(url);
        const fileNameDownload = fileName || `N-${nganhDaoTao}_K${khoa}`;
        axios({
            url,
            method: "POST",
            responseType: "blob", // important
            data: {
                data: JSON.stringify(data),
                fileName: fileNameDownload,
            },
        }).then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement("a");
            link.href = url;
            link.setAttribute("download", `${fileNameDownload}.pdf`);
            document.body.appendChild(link);
            link.click();
            setIsProcessing(false);
        });
    };

    return (
        <>
            {isProcessing && <LoadingBar />}
            {isLoading ? (
                <Loading />
            ) : (
                <div>
                    <Steps config={config}>
                        <div className="step_container">
                            <Step
                                order={1}
                                title={"STEP_1"}
                                component={Step1_Wrapper}
                                values={data}
                                saveValue={saveValue}
                            />
                        </div>

                        <div className="step_container">
                            <Step
                                order={2}
                                title={"STEP_2"}
                                component={CauTrucChuongTrinh}
                                values={data?.khoiKienThuc}
                                saveValue={saveValue}
                                saveKey="khoiKienThuc"
                            />
                        </div>
                        <div className="step_container">
                            <Step
                                order={3}
                                title={"STEP_3"}
                                component={ChuanDauRa}
                                values={data?.chuanDauRaNganhDaoTao || []}
                                saveValue={saveValue}
                                saveKey="chuanDauRaNganhDaoTao"
                            />
                        </div>
                        <div className="step_container">
                            <Step
                                order={4}
                                title={"STEP_4"}
                                component={NoiDungChuongTrinhWrapper}
                                values={data?.cauTrucChuongTrinh}
                                saveValue={saveValue}
                                saveKey="cauTrucChuongTrinh"
                            />
                        </div>
                        <div className="step_container">
                            <Step
                                order={5}
                                title={"STEP_5"}
                                component={KeHoachGiangDayWrapper}
                                values={data?.keHoachGiangDay}
                                saveValue={saveValue}
                                saveKey="keHoachGiangDay"
                            />
                        </div>
                        <div className="step_container">
                            <Step
                                order={7}
                                title={"STEP_7"}
                                component={PreviewPdf}
                                pathUrl={urlPreviewPdf}
                                values={{
                                    data: JSON.stringify(data),
                                    fileName: fileName,
                                }}
                                exportPdf={exportPdf}
                            />
                        </div>
                    </Steps>
                </div>
            )}
        </>
    );
};

export default EditorCTDTWrapper;
